    width: 100%;
    max-height: 66px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 101;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0px 25px 0px 0px;
    background-color: rgb(63, 63, 63);
    color: rgb(197, 197, 197);
    border-bottom: 1px solid rgb(95, 95, 95);
}

.dashboard-profile {
    display: grid;
    grid-template-columns: 1fr min-content;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    font-size: 19px;
}

.flex{
    display: flex;
    align-items: center;
}

.profileImage{
    width: 50px;
}

.clock {
    color: rgb(197, 197, 197);
    margin: 0;
}

.dashboard-name {
    font-size: 14px;
    cursor: pointer;
    margin: 0;
}

.dashboard-header>.dashboard-profile .content-img {
    margin: 0;
    width: 50px;
    height: 50px;
    border-width: 2.5px;
    border-color: transparent;
    border-radius: 50%;
    object-fit: cover;
    margin-left: auto;
}

.dashboard-logo {
    filter: invert(100);
    padding: 20px;
    padding-right: 0;
    height: 66px;
    text-align: left;
    object-fit: contain;
    object-position: 0;
    position: relative;
}

.sidebar{
    transition: width .5s ease-in-out;
    width: 65px;
}

.collapsed {
    width: 65px;
    height: calc(100vh - 88px);
    
    transition: width .25s ease-in-out;
    display: flex;
    flex-direction: column;
    position: fixed;

    min-height: calc(100vh - 66px);
    border-right: 1px solid rgb(95, 95, 95);
    background-color: rgb(63, 63, 63);
}

.divider{
    padding: 0 0 0 20px;
}

.platform-view{
    padding: 0 20px;
    display: inline-block;
    border-right: 1px solid;
    padding-right: 10px;
    margin-right: 10px;
}

.collapsed .hiddenCollapsed{
    opacity: 0;
    width: 0;
    visibility: hidden;
    transition: all .25s ease-in-out;
}

.sidebar:hover, .sidebar:hover>.collapsed, .sidebar.expand,.collapsed.expand {
    width: 170px;
}

.navOverlay{
    display: flex;
}

.collapsed nav {
    width: 100%;
}

.collapsed .navItems {
    color: #fff;
    display: flex;
    width: 100%;
    padding: 25px 15px;
    overflow: hidden;
    align-items: center;
    justify-content: center;
    white-space: nowrap;
    transition: width .15s ease-in-out;
    text-decoration: none;
}

.navItems.--active, .navItems:hover{
    background-color: rgb(95, 95, 95);
    color: rgb(241, 241, 241);
    transition: width .15s ease-in-out;
}

.collapsed:hover .navItems, .collapsed.expand a{
    width: 100%;
}

.collapsed:hover nav, .collapsed.expand nav{
    width: 150px;
}

.collapsed.expand .hiddenCollapsed, .collapsed:hover .hiddenCollapsed{
    opacity: 1;
    width: auto;
    visibility: visible;
}

.dashboard-icons{
    width: 25px;
    height: 25px;
    /* padding: 15px; */
    margin-right: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.collapsed .dashboard-icons{
    margin-right: 0px;
}

.collapsed.expand .dashboard-icons, .collapsed:hover .dashboard-icons{
    margin-right: 10px;
}

