    background-blend-mode: lighten;
    display: block;
    font-style: normal;
    font-size: 20px;
    width: 20px;
    height: 20px;
}

.expandBtn{
    float: right;
    padding: 10px;
    margin: 10px 0px;
    border: none;
    background: transparent;
    color: #fff;

    display: flex;
    align-items: center;
    justify-content: end;
    cursor: pointer;
}

.expandBtn::after{
    content: "";
    width: 20px;
    height: 20px;
    display: block;
    float: right;
    margin-left: auto;
    background:  url(${___CSS_LOADER_URL_REPLACEMENT_8___});
    background-size: contain;
    background-repeat: no-repeat;
    background-blend-mode: lighten;
}

.navItems--bottom{
    margin-top: auto;
}

.navLogout{
    margin-top: auto;
    background: transparent;
    border: none;
    width: 100%;
    color: #fff;
    padding: 30px 15px;
    font-size: 15px;
    text-align: center;
    border-top: 1px solid #636363;
    cursor: pointer;
    display: flex;
    align-items: center;

}

.dashboard-organisationSelector{
    border: none;
    padding: 0px 17px;
    background: transparent;
    color: #fff;
    font-size: 12px;
    appearance: none;
    -webkit-appearance: none;
    position: relative;
}

.dashboard-organisationSelector:focus, .dashboard-organisationSelector:focus-within, .dashboard-organisationSelector:focus-visible{
    outline: none;
}

.dashboard-organisationContainer{
    position: relative;
}

.dashboard-profile__nameContainer{
    width: 250px;
    margin-right: 20px;
    text-align: right;
}

.arrowRight{
    width: 10px;
    height: 10px;
    display: inline-block;
    margin-left: 10px;

}

.company_container{
    display: flex;
    justify-content: center;
    align-items: center;
}

.company_container .selectorContianer{
    margin-right: 20px;
}

.logo_container{
    display: flex;
    align-items: center
}

.logo-selector-container{
    display: flex;
    align-items: center;
}

@media screen and (min-width: 320px) and (max-width: 900px) {
    .dashboard-header {
        width: 100%;
        max-height: max-content;
        padding-right: 0;
    }

    .main-grid{
        padding-top: 104px;
    }

    .logo-selector-container{
        display: block;
    }

    .company_container{
        flex-direction: column;
        align-items: flex-start;
        padding: 0 20px;
        padding-bottom: 10px;
        gap: 10px;
    }

    .grid-3{
        grid-template-columns: 1fr;
    }

    .grid-container{
        display: block;
    }

    .dashboard-profile__nameContainer{
        width: auto;
    }

    .navOverlay{
        display: none;
    }

    .dashboard-header>.dashboard-profile .content-img{
        margin: 0 5px 5px auto;
    }

    .platform-view{
        border: none;
        padding: 0;
        margin: 0;
    }

    .infoHeader{
        top: 10px;
        position: relative;
    }

    .infoHeader.sticky{
        top: 144px;
    }

    .infoHeader .dashboard-content{
        grid-template-columns: .5fr 1fr !important;
    }

    .infoHeader .crawl-cta{
        width: max-content;
    }
